.form-section {
  background-color: $bg-primary;
}

.form-container {
  padding: 56px 18px;
}

.form {
  min-height: 336px;
}

.form-box {
  display: block;
  margin: 0 auto;
  background-color: $bg-body;
  min-width: 340px;
}

.action-text-box {
  padding: 20px 0 0 36px;
}

.action-text {
  font-size: 14px;
  font-weight: 300;
  color: $accent;
}

.form-subtitle-box {
  display: flex;
  justify-content: center;
  padding: 12px 0;
}

.form-subtitle {
  font-size: 20px;
  font-weight: 700;
  color: $primary-text;
  text-transform: uppercase;
}

.form-block {
  display: block;
  margin: 0 auto;
  width: 268px;
}

.data-field {
  display: block;
  position: relative;
  margin: 0 auto;
  margin-bottom: 20px;
}

.data-field__input {
  @include font(14px, 1.9, 0.07em, 500);
  border: none;
  border-radius: 0;
  border-bottom: 1px solid $form-line;
  background-color: transparent;
  min-width: 268px;
  min-height: 28px;
  color: $bg-dark-gray;
  padding-left: 4px;

  &:focus,
  &:hover {
    border-bottom: 1px solid $accent;
    outline: none;
    background-color: $bg-body;
  }
}

.data-field-text {
  position: absolute;
  top: 0px;
  left: 0px;
  padding: 5px 0 1px 3px;

  font-size: 14px;
  font-weight: 500;
  color: $form-text;

  &__name {
    transform: translateY(0%);

    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    .data-field:focus-within > & {
      transform: translateY(-18px);
      left: 0;
    }

    .data-field__input:not(:placeholder-shown) + & {
      transform: translateY(-18px);
      left: 0;
    }

    .data-field__input:not(:placeholder-shown) + & {
      // color: #00000000;
      overflow: hidden;
    }
  }

  &__comment {
    transform: translate(0);

    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    .data-field-text:focus-within > & {
      transform: translate(0);
      left: 0;
      color: #00000000;
    }

    .data-field__input:not(:placeholder-shown) + & {
      color: #00000000;
      overflow: hidden;
    }
  }
}

// POLICY
.policy-box {
  width: 269px;
}

.policy {
  display: flex;
  align-items: baseline;
  margin: -1px;
  position: relative;
  cursor: none;

  &__checkbox {
    position: absolute;
    margin: -1px;
    bottom: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;

    &:checked + .policy__icon {
      border: 0.7px solid $form-line;
      background-color: $accent;
      background-image: url('../images/checkmark.svg');
      background-size: contain;
      background-origin: border-box;
    }
  }

  &__icon {
    display: inline-block;
    min-width: 18px;
    min-height: 18px;
    position: relative;
    top: -7px;
    left: -6px;
    transform: translate(50%, 50%);
    border: 1px solid $form-line;
    // margin-right: 20px;
  }

  &__text {
    @include fontMini(12px, 1.4, 400);
    color: $form-text;
    margin-left: 16px;
  }
}

.checkbox-box {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
}
